import React from 'react'
import PropTypes from 'prop-types'

export default class TwitchMenuButton extends React.Component {

  static get propTypes(){
    return {
      user: PropTypes.object
    }
  }

  constructor(props){
    super(props)
    this.state = {
      showMenu: false
    }
  }

  render(){
    return (
      <div className='twitch-menu-button'>
        <button className='twitch-button' onClick={this._showMenu.bind(this)}>
          <i className='fab fa-twitch'/>
          {this.props.user.name}
          <i className='fas fa-caret-up'/>
        </button>
        { this.state.showMenu ? this._makeMenu() : null }
      </div>
    )
  }

  _makeMenu(){

    const menuItems = []

    if(this.user.channel.registered){
      menuItems.push(<button onClick='this._unregister.bind(this)'>Unregister Your Channel</button>)
    }else{
      menuItems.push(<button onClick='this._register.bind(this)'>Register Your Channel</button>)
    }

    menuItems.push(<button onClick='this._logout.bind(this)'>Logout</button>)

    return (
      <div className='menu'>
        { menuItems }
      </div>
    )
  }

  _register(){
    console.log('Register')
    // Modal.show()
  }

  _unregister(){
    console.log('Unregister')
    // Modal.show()
  }

  _logout(){
    console.log('Logout')
  }

  _showMenu(event) {
    event.preventDefault()
    this.setState({ showMenu: true }, () => {
      document.addEventListener('click', this._closeMenu)
    })
  }

  _closeMenu() {
    this.setState({ showMenu: false }, () => {
      document.removeEventListener('click', this._closeMenu)
    })
  }
}